.Footer{height:516px;font-family:Roboto,sans-serif;background-color:#000}.Footer__info{padding:100px 32px 50px;display:flex;justify-content:space-between;align-items:baseline}.Footer__subscribe{display:flex;flex-direction:column;gap:26px;max-width:350px;font-family:Montserrat,sans-serif;line-height:19px;color:gray}.Footer__logo{line-height:52px;text-decoration:none;font-family:Kodchasan,sans-serif;font-size:40px;font-weight:500;color:#fff}.Footer__text{font-size:16px}.Footer__text--size-24{font-size:24px}.Footer__text--color-light{color:#a9a9a9}.Footer__form{display:flex;align-items:center;width:344px}.Footer__form input{box-sizing:border-box;padding:12px;width:75%;height:40px;font-size:14px;border:1px solid #444343;outline:none;background-color:rgba(0,0,0,0);color:#fff}.Footer__form input:-webkit-autofill,.Footer__form input:-webkit-autofill:focus{transition:background-color 0s 600000s,color 0s 600000s}.Footer__button{padding:5px 10px;width:25%;height:40px;border:none;transition:all .5s;font-size:16px;background-color:#fff !important;color:#000}.Footer__button:hover{background-color:#71b360}.Footer__share{display:flex;gap:12px}.Footer__icon{width:36px;height:36px;transition:all .3s;transition-timing-function:ease-out;color:rgba(0,0,0,0)}.Footer__icon:hover{transform:scale(1.2)}.Footer__icon--twitter{background-image:url("../../img/icon-twitter.svg")}.Footer__icon--facebook{background-image:url("../../img/icon-facebook.svg")}.Footer__icon--linkedin{background-image:url("../../img/icon-linkedin.svg")}.Footer__icon--instagram{background-image:url("../../img/icon-instagram.svg")}.Footer__catalog{position:relative;top:50px}.Footer__line{margin:0 32px;border:1px solid #444343}.Footer__copyright{display:flex;justify-content:center;align-items:center;padding:36px 0 0;font-family:Montserrat,sans-serif;font-size:12px;font-weight:400;letter-spacing:1px;color:#fff}.Footer__copyright::before{content:"©";position:relative;right:5px}.catalog{display:flex;flex-direction:column;gap:10px}.catalog__title{padding-left:10px;line-height:26px;font-family:Montserrat,sans-serif;font-size:16px;font-weight:700;color:#b7b6b6}.catalog__link{padding-left:10px;line-height:20px;text-decoration:none;font-family:Montserrat,sans-serif;font-size:14px;font-weight:300;transition:all .2s;color:#b7b6b6}.catalog__link:hover{transform:translateX(10px)}.year{padding-right:5px}